$bsx-checklist-indent:              2em !default;


$bsx-checklist-icon-content:        \f00c !default; // unquoted
$bsx-checklist-icon-font-family:    $bsx-icon-font-family !default;
$bsx-checklist-icon-color:          $primary !default;
$bsx-checklist-icon-font-weight:    $bsx-icon-font-weight !default;


$bsx-checklist-item-spacing:        .25em !default;



// style

.checklist {
  list-style: none;
  padding-left: $bsx-checklist-indent;

  li {
    &:before {
      content: unquote("\"#{$bsx-checklist-icon-content}\"");
      //position: absolute;
      font-family: $bsx-checklist-icon-font-family;
      display: inline-block;
      width: $bsx-checklist-indent;
      margin-left: -$bsx-checklist-indent;
      color: $bsx-checklist-icon-color;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      text-rendering: auto;
      font-weight: $bsx-checklist-icon-font-weight;
    }

    & + li {
      padding-top: $bsx-checklist-item-spacing;
    }
  }
}